
.model {
  display: inline-block;
  padding: 1rem 2rem;
  background-color: #fff;
  position: fixed;
  z-index: 9999;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  border-radius: 0.3rem;
  width: 22rem;
  top: 10rem;
}
.model .icon-close {
  position: absolute;
  right: 1rem;
  top: 1rem;
  cursor: pointer;
}
.model label::after{
  display: block;
  width: 0px;
  height: 0px;
  overflow: hidden;
  content: '';
  clear: both;
  margin-bottom: 1rem;
}
.model label {
  display: inline-block;
  position: relative;
  width: 100%;
}
.model .icon-user,
.model .icon-password{
  left: 1rem;
  position: absolute;
  top: 0.5rem;
  color: #555;
}
.model h2 {
  font-size: 1.2rem;
  margin: 0.5rem 0;
  display: inline-block;
  width: 100%;
}
.model input{
  outline: none;
  border: 1px solid #d8d2d2;
  line-height: 2rem;
  font-size: 1rem;
  padding-left: 2rem;
  border-radius: 0.2rem;
  color: #000;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.model .btn{
  font-size: 1rem;
  text-align: center;
  width: 100%;
  line-height: 1.8rem;
  background-color: #337ab7;
  color: #fff;
  font-weight: bold;
  letter-spacing: 0.2rem;
  border: 1px solid #2e6da4;
}
.model .btn:hover{
  background-color: #204d74;
  border-color: #122b40;
}
.mark {
  top: 0px;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 9998;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: all .5s;
  transition: all .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0;
  top: -20rem;
}
.btngroup {
  margin: 1rem 0px;
}
.btngroup .btn {
  width: 40%;
  border-radius: 0.2rem;
}
.btngroup .cancel {
  background-color: #fff;
  border-color: #ccc;
  color: #333;
}
.btngroup .cancel:hover{
  background-color: #ccc;
  border-color: #333;
}



